import { SApp } from './assets/styles/app.styles';
import AddTodo from './components/addTodo';
import TodoCount from './components/todoCount';
import TodoFilter from './components/todoFilter';
import TodoList from './components/todoList';
import { useState, useEffect } from 'react';
import { ITodo } from './todo/model';
import { Container, Typography } from '@mui/material';

const getLocaleStorage = () => {
  let list = localStorage.getItem('todos');
  if (list) {
    return JSON.parse(list);
  } else {
    return [] as ITodo[];
  }
};

function App() {
  const [todos, setTodos] = useState<ITodo[]>(getLocaleStorage());
  const [filtered, setFiltered] = useState(todos);

  useEffect(() => {
    localStorage.setItem('todos', JSON.stringify(todos));
    setFiltered(todos);
  }, [todos]);

  return (
    <SApp>
      <Container maxWidth="sm">
        <Typography variant='h2' textAlign='center'>Todo List</Typography>
        <AddTodo setTodos={setTodos} todos={todos} />
        <TodoFilter todos={todos} setFiltered={setFiltered} />
        <TodoCount count={todos.length} />
        <TodoList todos={todos} setTodos={setTodos} filtered={filtered} />
      </Container>
    </SApp>
  );
}

export default App;
