import { useState } from "react"
import { ITodo } from "../todo/model"
import { v4 as uuidv4 } from 'uuid'
import { Button, Input, Box } from '@mui/joy'

interface AddTodoProps {
	todos: ITodo[]
	setTodos: (arg: ITodo[]) => void
}

function AddTodo({todos, setTodos}: AddTodoProps) {

	const [value, setValue] = useState('')

	function addTodo() {
		if(value) {
			setTodos(
				[...todos, {
					id: uuidv4(),
					title: value,
					completed: false,
				}]
			)
			setValue('')
		}
	}

	function handleChange (event: React.ChangeEvent<HTMLInputElement>) {
		setValue(event.target.value)
	}

	return (
		<Box display='flex' justifyContent='space-between'>
			<Input sx={{width: '100%', marginRight: '5px'}}
			color="success"
			variant="soft"
			size="sm"
			placeholder="New todo..." 
			value={value} 
			onChange={handleChange}
			/>
			<Button variant="outlined" color="success" onClick={addTodo}>Add</Button>
		</Box>
	)
}
export default AddTodo